//组件应该从父组件中获取商品的信息
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,Params } from '@angular/router'
import 'rxjs/add/operator/switchMap'

import { GoodDetailService  } from '../good-detail.service'

@Component({
  selector: 'app-stuffdetail',
  templateUrl: './stuffdetail.component.html',
  styleUrls: ['./stuffdetail.component.css']
})
export class StuffdetailComponent implements OnInit {
	img:string = "12.jpg";
	//goodDesc为由服务通过get请求得到的关于商品详情的对象
  //对象的内容包括{imgUrls:[],title:,price:,desc:,distance:,}
	goodDesc:Object;
	//commonts 为每个商品下的评论，可以由服务通过getCommonts获取
  //每个评论对象的形式为{headImg:,time:,text:, }
	commonts:Object;
	stuffId:number;
	constructor(private Detail:GoodDetailService,private route:ActivatedRoute) { }

	ngOnInit() {
	  //通过上级路由获取传入的商品id，并得到该id对应下的商品信息
    this.route.params
      .switchMap((params:Params)=>  this.Detail.getDesc(+params['id']))
      .subscribe(Desc=>this.goodDesc=Desc);
  	}

}



