//跳转到此页的触发页只能是在'我的商品'，点击商品的编辑和添加商品跳转
//当点击编辑按钮进入的时候，需要获取商品id并在进入之前获取商品的信息并填写号表单
//当点击添加商品进入的时候，不需要从后台获取商品的内容，显示空表单即可
import { Component, OnInit } from '@angular/core';

import { ActivatedRoute,Params } from '@angular/router'
import 'rxjs/add/operator/switchMap'

import { JsonOfForm} from '../formatOfDetail'
import { GoodDetailService  } from '../good-detail.service'
@Component({
  selector: 'app-submit-form',
  templateUrl: './submit-form.component.html',
  styleUrls: ['./submit-form.component.css']
})
export class SubmitFormComponent implements OnInit {
  uploadFiles:any[];
  //JsonOfForm 用来记录表单中的所有内容
  JsonForm: JsonOfForm = new JsonOfForm();

  constructor(private route:ActivatedRoute,private Detail:GoodDetailService) {
   }

   //当选择一张图片的时候执行此事件，将图片在相应的div中预览
  uploadChange(files){
  	console.log(this.JsonForm.pictures.length);
  	this.JsonForm.pictures.push(window.URL.createObjectURL(files[0]));
  }

  onSubmit(){
  	console.log(this.JsonForm);
  }

  id:number;
  ngOnInit() {
    //通过上级路由获取传入的商品id，并得到该id对应下的商品信息
    this.route.params
      .switchMap((params:Params)=> {this.id=params['id'];return this.Detail.getForm(params['id'])})
      .subscribe(Form=>{this.JsonForm=Form;console.log(Form,this.id)});
  }

}



