import { Component, OnInit } from '@angular/core';
import { StuffinfoComponent } from '../stuffinfo/stuffinfo.component'

import { GoodslistService } from '../goodslist.service'
import { BriefIntro } from '../briefIntro'

@Component({
  selector: 'app-mystuff',
  templateUrl: './mystuff.component.html',
  styleUrls: ['./mystuff.component.css']
})
export class MystuffComponent implements OnInit {
  /* init-------------------------------------------------------------------*/
  navBarState:Boolean[] = [true,false,false];
  //fouceGoods为关注的商品列表，由GoodslistSerivce的getGoods方法通过get请求获取
  focusGoods:BriefIntro[];

  //只有在进入我的商品的时候，ismystuff才可以为true,使用这个参数来控制是否可以对商品信息进行修改
  ismystuff:Boolean = false;
  constructor(private GoodslistService : GoodslistService ) { }

  //optionsForGet 为使用GoodslistService服务获取商品列表的配置项
  optionsForGet = {
    source:'focus',class:'all',search:''
  }

  ngOnInit() {
    //通过GoodlistService远端获取关注的
    this.focusGoods = this.GoodslistService.getGoods({})
  }


  /* methods-------------------------------------------------------------------*/
  //当点击菜单栏的时候，改变focusGoods来在我关注的商品和我的售卖商品中切换
  clickNavbar(num){
    if(num == 0 ){
      this.optionsForGet.source = 'focus';
      this.ismystuff = false;
    }else{
      this.optionsForGet.source = 'myStuff';
      this.ismystuff = true;
    }
    //通过GoodslistService服务获取商品列表
    this.focusGoods = this.GoodslistService.getGoods(this.optionsForGet);
    //每点击一次对顶栏的状态进行切换
  	for(var i in this.navBarState){
  		if(i == num){
  			this.navBarState[i] = true;
  		}
  		else{
  			this.navBarState[i] = false;
  		}
  	}
  }



}



