import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule ,JsonpModule} from '@angular/http';
import { RouterModule } from '@angular/router'

import { AppComponent } from './app.component';
import { SubmitFormComponent } from './submit-form/submit-form.component';
import { HomeComponent } from './home/home.component';
import { FooterComponent } from './footer/footer.component';
import { MystuffComponent } from './mystuff/mystuff.component';
import { StuffinfoComponent } from './stuffinfo/stuffinfo.component';
import { StuffdetailComponent } from './stuffdetail/stuffdetail.component';
import { GoodslistService } from './goodslist.service';
import { GoodDetailService } from './good-detail.service'
import { CommontComponent } from './commont/commont.component';
import { MyStuffInfoComponent } from './my-stuff-info/my-stuff-info.component'

@NgModule({
  declarations: [
    AppComponent,
    SubmitFormComponent,
    HomeComponent,
    FooterComponent,
    MystuffComponent,
    StuffinfoComponent,
    StuffdetailComponent,
    CommontComponent,
    MyStuffInfoComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    JsonpModule,
    RouterModule.forRoot([

      {path:"home",component:HomeComponent},
      {path:"sale",component:SubmitFormComponent},
      {path:'mystuff',component:MystuffComponent},
      {path:'detail/:id',component:StuffdetailComponent},
      {path:'form/:id',component:SubmitFormComponent}
    ])
  ],
  providers: [GoodslistService,GoodDetailService],
  bootstrap: [AppComponent]
})

export class AppModule {
  announcerId:number = 0;
 }



