//本文件用于定义表单的格式，发送和接收的格式
export class JsonOfForm{
  "stuffName":string;//商品名称
  "stuffPrice":number;//商品价格
  "stuffDetail":string;//商品描述
  "stuffKind":number;//商品类型
  "organization":number;//商品
  "contactPhone":number;//商品发布者联系电话
  "weChatId":string;//商品发布者的微信id
  "showOrNot":number;//是否显示联系信息
  "pictures":string[]//添加的图片地址

}



