import { Component, OnInit} from '@angular/core';

import { BriefIntro } from '../briefIntro'
import {GoodslistService} from '../goodslist.service'
import {StuffinfoComponent} from '../stuffinfo/stuffinfo.component'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  //goods为要在首页展示的商品信息，由GoodslistService服务中的getGoods通过get请求获取
  //传入的参数为{source:'home',...}
  public goods:BriefIntro[];
  selectGood : number;
  ismystuff : false;
  constructor(private GoodslistService:GoodslistService) {

   }
   //当进行搜索时调用此函数，函数调用服务从服务器获取查找的内容，并设置goods属性
  searchGoods(searchText){}
  ngOnInit() {
    this.goods = this.GoodslistService.getGoods({});
  }

}



